const Plate = styled.div`
  background-color: gray;
  width: inherit;
  height: inherit;
  margin: 12px;
  border-radius: 50%;
  position: relative;

  ::after,
  ::before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
  }

  ::after {
    top: 35px;
    right: 35px;
    bottom: 35px;
    left: 35px;
    border: 6px solid #b2854b;
    box-shadow: inset 0px 0px 0px 2px #b2854b, 0px 0px 0px 2px #b2854b;
  }

  ::before {
    background: black;
    border: 1px solid silver;
    box-shadow: inset 0px 0px 0px 2px #808080;
    top: 30%;
    left: 30%;
    right: 30%;
    bottom: 30%;
    z-index: 1;
  }
 
  @media only screen and  (max-width: 375px) {
    ::before {
      top: 32%;
    left: 32%;
    right: 32%;
    bottom: 32%;
    }
    left:-10px;

    ::after {
      top: 29px;
      right: 29px;
      bottom: 29px;
      left: 29px;

    }
  
  }

`;

const BackPlate = styled.p`
  position:absolute;
  width: calc(100% );
  height:  calc(100%);
  background-color:black;
  // left:-34px;
  // bottom:-51px;
  border-radius:100%;
  
`

