import React from 'react';
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
    },
    textField: {
        width: "25ch",
    },
    fontColor: {
        borderColor: "white",
    },
}));

function Control(props){
    const classes = useStyles();
    const {spinTheWheel, pendingTotalBet, isSpinComplete, chipCount, previousTotalBet, isWheelSpinning, resetLayout, recentBets, undoRecentBet} = props;

    return(
        <div className = "text-center mt-4">
       
            <div className = "mt-3 mb-3">
            {!isSpinComplete ? (
                <div>
                    {chipCount >= previousTotalBet && previousTotalBet > 0 ? (
                    <>
                        
                    </>
                    ) : (
                    <></>
                    )}
                    {pendingTotalBet > 0 && !isWheelSpinning ? (
                    <button onClick={resetLayout} className="x-action-button mr-2 ml-2">
                        Reset
                    </button>
                    ) : (
                    <></>
                    )}
                    {recentBets.length > 0 && !isWheelSpinning? (
                    <button onClick={undoRecentBet} className="x-action-button mr-2 ml-2">
                        Undo
                    </button>
                    ) : (
                    <></>
                    )}
                </div>
                ) : (
                <div className="filler-div"></div>
                )}
            </div>
            <div>
                <TextField
                        defaultValue="0"
                        value = {pendingTotalBet}
                        id="outlined-start-adornment"
                        className={clsx(
                            classes.margin,
                            classes.textField,
                            classes.fontColor
                        )}
                        label="Total bet"
                        InputProps={{
                            readOnly: true,
                        }}
                        variant="outlined"
                    />
                <button className = "x-play-button " onClick={spinTheWheel}>Play</button>
            </div>
            
        </div>
    )
}

export default Control;